/****** dropdown-select *******/

.wrap-dd-select {
  position: relative;

  display: flex;
  align-items: center;
  margin-left: -5px;
  padding: 4px 4px 4px 10px;

  font-size: 1.6rem;

  -webkit-user-select:none;
  -moz-user-select:none;
  user-select:none;

  background: #fff;
  border-radius: 4px;
  cursor: pointer;
  outline: none;
  color: #8AA8BD;

  li {
    margin: 0;
  }
}

.wrap-dd-select.disabled {
  cursor: default;
  color: #5B6A7D;
}
.wrap-dd-select:after {
  content: "";
  width: 0;
  height: 0;
  position: absolute;
  right: 15px;
  top: 50%;
  margin-top: -3px;
  border-width: 6px 6px 0 6px;
  border-style: solid;
  border-color: #8aa8bd transparent;
}

.wrap-dd-select.disabled:after {
  border: none;
}

.wrap-dd-select .dropdown {
  margin-top:20px;
  position: absolute;
  top: 100%;
  left: 0;
  right: 0;
  z-index: 10;
  background: white;
  padding: 0;
  border-radius: inherit;
  border: 1px solid rgba(0,0,0,0.17);
  box-shadow: 0 0 5px rgba(0,0,0,0.1);
  font-weight: normal;
  list-style: none;
  visibility: hidden;
  opacity: 0;
  transition:visibility 0s linear 0.2s,opacity 0.2s linear;

  max-height: 300px;
  overflow-y: auto; /*Provide an auto overflow to display scroll*/
}

.wrap-dd-select .dropdown li.divider {
  padding: 2px 0;
  background: #e6e8ea;
}

.wrap-dd-select .dropdown li.divider-label {
  background: #e6e8ea;
  cursor: default;
  color: #000;
  padding: 10px 0;
}

.wrap-dd-select .dropdown li a {
  display: block;
  padding: 10px;
  text-decoration: none;
  color: #8aa8bd;
  border-bottom: 1px solid #e6e8ea;
  box-shadow: inset 0 1px 0 rgba(255,255,255,1);
  cursor: pointer;
}

.wrap-dd-select .dropdown li i {
  float: right;
  color: inherit;
}

.wrap-dd-select .dropdown li:first-of-type a {
  border-radius: 4px 4px 0 0;
}

.wrap-dd-select .dropdown li:last-of-type a {
  border-radius: 0 0 4px 4px;
  border: none;
}

.wrap-dd-select .dropdown li:hover a {
  background: #f3f8f8;
}

.wrap-dd-select .dropdown:after {
  content: "";
  width: 0;
  height: 0;
  position: absolute;
  bottom: 100%;
  right: 15px;
  border-width: 0 6px 6px 6px;
  border-style: solid;
  border-color: #fff transparent;
}

.wrap-dd-select .dropdown:before {
  content: "";
  width: 0;
  height: 0;
  position: absolute;
  bottom: 100%;
  right: 13px;
  border-width: 0 8px 8px 8px;
  border-style: solid;
  border-color: rgba(0,0,0,0.1) transparent;
}

.wrap-dd-select.active .dropdown {
  visibility: visible;
  opacity: 1;
  transition: opacity linear 0.2s;
}

/****** dropdown-menu *******/
